"use strict";
class BaseLoader {
    constructor(option) {
        this.option = option;
    }
    load(context) {
        return this.contextDef
            .then(def => {
            return this.loadTasks(context, def);
        })
            .catch(err => {
            console.error(err);
        });
    }
    loadContext(env) {
        let self = this;
        return this.contextDef
            .then(def => {
            return def.getContext({
                option: self.option,
                env: env
            });
        })
            .catch(err => {
            console.error(err);
        });
    }
    get contextDef() {
        if (!this._contextDef) {
            this._contextDef = Promise.resolve(this.getContextDefine());
        }
        return this._contextDef;
    }
    loadTasks(context, def) {
        if (def.tasks) {
            return def.tasks(context);
        }
        else {
            let mdl = this.getTaskModule();
            return context.findTasks(mdl);
        }
    }
    getConfigModule() {
        let loader = this.option.loader;
        return loader.configModule || loader.module;
    }
    getTaskModule() {
        let loader = this.option.loader;
        return loader.taskModule || loader.module;
    }
}
exports.BaseLoader = BaseLoader;
